.filter{
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-left: 25px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
  color: black;
}
.filter:hover{
  border-color: silver;
  transform: scale(1.1);
}